import React, { Component } from "react";
import { AccordionContentStyle1 } from "./general/style/style";
// import { toJS } from "mobx";

export default class SpecCompareTable extends Component {
  render() {

    return (
      <div className="accordItemContent">
        <AccordionContentStyle1>
          <table className="tableData" style={{width:"100%"}}>
            <tbody>
              { this.props.rows && this.props.rows.map((el)=>(
                  <tr>
                    <td>
                      {this.props.showTableLeft &&
                        <div>
                          <em className="bold">{el.product1.specName}</em>
                          <span>{el.product1.value}</span>
                        </div>
                      }
                    </td>
                    <td>
                      {this.props.showTableRight && 
                        <div>
                          <em className="bold">{el.product2.specName}</em>
                          <span>{el.product2.value}</span>
                        </div>
                      }
                    </td>
                  </tr>
              ))}
            </tbody>
          </table>
        </AccordionContentStyle1>
      </div>
    );
  }
}
